export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "sport": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Sport Jumps"])},
        "courses": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Courses"])},
        "passengers": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Passengers"])}
      },
      "es": {
        "sport": (ctx) => {const { normalize: _normalize } = ctx;return _normalize([])},
        "courses": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Cursos"])},
        "passengers": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Pasajeros"])}
      }
    }
  })
}
